@import url(./config/config.css);

.hideRight {
    animation: fadeOutRight 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;
}

.hideLeft {
    animation: fadeOutLeft 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;
}


.showLeft {
    animation: fadeInAnimationLeft ease 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;
}

.showRight {
    animation: fadeInAnimationRight ease 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;
}

.simpleHide {
    animation: opacityBye ease 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;

}

@keyframes opacityBye {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.simpleShow {
    animation: opacityWelcome ease 1s;
    animation-iteration-count: unset;
    animation-fill-mode: forwards;

}

@keyframes opacityBWelcome {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadeInAnimationLeft {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes fadeInAnimationRight {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes fadeOutLeft {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }

    100% {
        opacity: 0;
        transform: translateX(-100px);
    }
}

@keyframes fadeOutRight {
    0% {
        opacity: 1;
        transform: translateX(0px);
    }

    100% {
        opacity: 0;
        transform: translateX(100px);
    }
}




